import md5 from 'js-md5'; // md5加密
import axios from 'axios'  //axios
var apitoken = Date.parse(new Date()) + 'wechatbak';
export default {
	time: Date.parse(new Date()),
	apitoken: md5(apitoken),
	getAxio(url,data){
		return axios({
				url: url,
				method: 'post',
				data: data
			})
			.then(res => {	
				return res.data;
			})
	}

}


