<template>
	<el-container>
		<el-header>
			<el-col class="header">
				<el-col class="logo" :span="4" :style="{width: width}">微信开发者后台</el-col>
				<el-col :span="10">
					<div class="tools" @click="change">
						<i class="fa fa-align-justify"></i>

					</div>
					<div class="nowPublic" >
						当前公众号 : <span>{{nowPublic}}</span>
					</div>
				</el-col>
				<el-col :span="3" class="information">
					<el-dropdown trigger="click" style="cursor: pointer;">
						<span class="el-dropdown-link">
						    {{username}}<i class="el-icon-arrow-down el-icon--right"></i>
						  </span>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item @click.native="loginOut">
								退出登录
							</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</el-col>
			</el-col>
		</el-header>
		<el-container class="main-container">
			<el-aside :style="{width: width}">
				<el-menu @open="handleOpen" @close="handleClose" :collapse="isCollapse" :router="router" :collapse-transition="collapseTransition" :default-active="activeIndex" active-text-color="#F56C6C" text-color="#909399">
					<el-submenu index="1">
						<template slot="title">
							<span>公众号管理</span>
						</template>
						<el-menu-item-group>
							<el-menu-item index="configure">公众号配置</el-menu-item>
							<el-menu-item index="menu">自定义菜单</el-menu-item>
							<el-menu-item index="autoReply">自动回复配置</el-menu-item>
						</el-menu-item-group>
					</el-submenu>
					<el-submenu index="2">
						<template slot="title">
							<span>消息推送</span>
						</template>
						<el-menu-item-group>
							<el-menu-item index="2-1">模板消息</el-menu-item>
						</el-menu-item-group>
					</el-submenu>
					<el-submenu index="3">
						<template slot="title">
							<span>系统管理</span>
						</template>
						<el-menu-item-group>
							<el-menu-item index="3-1">权限设置</el-menu-item>
							<el-menu-item index="3-2">系统日志</el-menu-item>
						</el-menu-item-group>
					</el-submenu>
				</el-menu>
			</el-aside>
			<el-main>
				<router-view @nowPublic="getnowPublic" @activeIndex="getactiveIndex"/>
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
	export default {
		data() {
			return {
				router: true,
				isCollapse: false,
				collapseTransition: false,
				width: '200px',
				activeIndex: 'configure',
				username: '',
				nowPublic: ''
			};
		},
		mounted() {
			this.$nextTick(function() {
				if(localStorage.getItem("password")) {
					this.username = JSON.parse(localStorage.getItem("password")).username
				} else {
					this.$router.push({
						 name: 'login',
						 path: '/login'
					});
				}
			});
		},
		methods: {
			handleOpen(key, keyPath) {

			},
			handleClose(key, keyPath) {

			},
			change() {

				if(this.isCollapse) {
					this.width = "200px";

				} else {
					this.width = "60px";
				}
				this.isCollapse = !this.isCollapse;
			},
			loginOut() {
				let data = {
					time: this.tools.time,
					apitoken: this.tools.apitoken
				}
				this.tools.getAxio(process.env.API_HOST+'/index/Login/logout.html', data).then(res => {
					if(res.status == 1) {
						this.$router.push({
							 name: 'login',
							 path: '/login'
						});
					} else {
						this.$message.error(res.msg);
					}
				});
			},
			getnowPublic(val) {
				this.nowPublic = val
			},getactiveIndex(val){
				this.activeIndex = val
			}
		}
	}
</script>

<style scoped>
	.el-header {
		background-color: #20a0ff;
		color: #333;
		text-align: center;
		line-height: 60px;
		padding: 0;
	}
	
	.el-header .logo {
		border-right: 1px solid #fff;
		color: #fff;
		font-size: 20px;
	}
	
	.tools {
		padding: 0 23px;
		width: 14px;
		height: 60px;
		line-height: 60px;
		cursor: pointer;
		color: #fff;
		float: left;
	}
	
	.information {
		float: right;
	}
	
	.el-dropdown-link {
		color: #fff;
		font-size: 20px;
	}
	
	.el-aside {
		color: #333;
	}
	
	.el-menu {
		background-color: #eef1f6;
		height: 100%;
		border: 0;
	}
	
	.main-container {
		position: absolute;
		top: 60px;
		bottom: 0;
		overflow: hidden;
		width: 100%;
	}
	
	.el-main {
		color: #333;
		flex: 1;
	}
	
	.nowPublic {
		float: left;
		font-size: 16px;
		color: #fff
	}
	
	.nowPublic span {
		color: red
	}
	.el-menu-item.is-active{
		background: #ecf5ff;
	}
</style>


